.h-list {
  font-size: clamp(18px, 2.4vw, 20px);

  @include md {
    font-size: clamp(16px, 3.4vw, 20px);
    font-weight: 600; }

  @include sm {
    font-size: clamp(18px, 3.4vw, 24px); }

  &__item {
    display: flex;
    gap: 0 5px;

    @include sm;

    @include xs {
      gap: 0; } }

  &__icon {}

  &__value {
    display: inline-flex;

    @include md {
      color: $themeColor2; } }

  &__text {
    display: inline-flex;
    gap: 0 5px;

    br {
      display: none; }

    @include xs {
      display: block; } }

  &__text-top {

    @include md {
      color: $themeColor2; } } }

.info-container {
  padding: 42px 0 24px;
  @include md {
    padding: 24px 0; }
  &__inner {
    display: flex;
    justify-content: center;
    @include xs {
      flex-direction: column;
      align-items: center; }
    .info {
      &:not(:last-child) {
        margin-right: 106px;
        @include sm {
          margin-right: 50px; }
        @include xs {
          margin: 0 0 20px; } } } } }
