.s-workshops-list {
  padding: clamp(50px, 5vw, 100px) 0;

  .container {
    max-width: 1260px; }

  &__top {
    display: flex;
    justify-content: space-between;
    flex-direction: column-reverse;
    margin-bottom: clamp(24px, 4vw, 42px);
    gap: 50px;
    position: relative;

    @include sm {
      gap: 0;
      flex-direction: column-reverse; }

    .section {
      &__title {
        color: $white;
        aspect-ratio: 2.18;
        width: 100%;
        max-width: none;
        padding: 50px;
        position: absolute;
        left: 0;
        top: 0;
        margin: 0;

        @include md {
          padding: 30px; }

        @include sm {
          padding: 20px; } }

      &__subtitle {
        max-width: none;
        text-align: left;
        line-height: 1.78;
        margin: 24px 0;
        font-weight: 400;
        font-size: clamp(16px, 3vw, 18px); }

      &__info {
        max-width: none;
        text-align: left;
        line-height: 1.78;
        margin: 24px 0;
        font-weight: 400;
        font-size: clamp(14px, 3vw, 16px);

        @include sm {
          margin-bottom: 0; }

        a {
          color: $themeColor2;
          text-decoration: none;
          font-size: 18px;
          @include medium;
          font-weight: 600; }

        ul {
          display: flex;
          flex-direction: column;
          gap: 10px;
          margin: 16px 0 0;

          li {
            display: flex;
            gap: 20px;
            align-items: center;
            border: 1px solid $gray;
            padding: 18px 30px;
            border-radius: 10px;
            justify-content: space-between;

            @include sm {
              padding: 10px 20px;
              gap: 0;
              flex-direction: column; }

            a {
              &:hover {
                text-decoration: underline; } } } }


        strong {
          font-size: 20px;
          @include bold;
          font-stretch: 120%; }

        p {
          &:not(:first-child) {
            margin: 10px 0 0; } } } } }

  &__desc {
    max-width: none;
    width: 100%;
    max-width: 680px;
    margin: 0;
    padding: 0 50px;
    z-index: 1;

    @include md {
      padding: 0; } }

  &__img {
    width: 100%;
    flex: 1 1 auto;

    @include sm {
      margin: 0 auto; } } }

.workshops-imgs {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  aspect-ratio: 2.18;

  @include sm {
    aspect-ratio: 1.2;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 12px; }

  &__item {
    position: relative;

    &:first-child {
      grid-area: 1 / 1 / 3 / 3;

      img {
        border-radius: 20px; } }

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 10px;
      filter: brightness(0.8); } } }

.workshops-list {
  margin: 40px auto;

  .container {
    max-width: 1210px;
    width: 100%; }


  &__item {
    position: relative;
    display: flex;
    border-top: 1px solid $gray;
    color: #aaa;
    background: transparent;


    &.open {
      color: $white;
      background: #1B1B1B; }

    &:last-child {
     border-bottom: 1px solid $gray; }


    @include sm {
      flex-direction: column; } }

  &__item-overlay {
    position: relative;
    padding: 32px 0;
    display: flex;
    width: 100%;
    gap: 20px;
    z-index: 1;

    @include sm {
      gap: 10px;
      padding: 30px 0 30px;
      flex-direction: column; } }

  &__type {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 64px;
    font-size: 10px;
    height: 44px;
    font-size: 14px;
    text-transform: uppercase;
    margin: 0 0 0 0;
    order: 1;

    @include sm {
      height: 40px;
      flex: 1 1 auto;
      max-width: 50px;
      justify-content: flex-start;
      margin: 0;
      position: absolute;
      right: 0;
      top: 30px; } }

  &__info {
    flex: 1 1;

    @include sm {
      order: 3; } }

  &__title {
    font-size: clamp(24px, 2.6vw, 30px);
    @include black;
    line-height: 1.1;
    color: $white;
    display: block;
    text-decoration: none;
    &:hover {
      text-decoration: underline; } }

  &__spoiler {
    display: none; }

  &__desc {
    margin: 10px 0 0;
    font-size: 14px;
    line-height: 1.6; }

  &__date {
    font-size: 14px;
    margin: 4px 0 0;

    a {
      color: $themeColor2; } }

  &__speakers {
    display: flex;
    flex-direction: column;
    flex: 0 0 200px;
    gap: 5px;
    order: 2;

    @include tablet {
      flex: 0 0 200px; }

    @include sm {
      flex-direction: row;
      justify-content: flex-start;
      flex: 1 1 auto;
      margin: 0;
      gap: 5px;
      flex-direction: column; } }

  &__speaker {
    display: flex;
    align-items: center; }

  &__speaker-photo {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    margin: 0 16px 0 0;

    @include sm {
      width: 40px;
      height: 40px; } }

  &__speaker-name {
    line-height: 1.34;
    font-size: 14px; }

  &__buttons {
    flex: 0 0 210px;
    display: flex;
    flex-direction: column;
    gap: 5px;

    @include sm;

    .btn {
      padding: 3px 15px;
      min-height: 50px;
      margin: 16px 0 0;
      @extend .btn;
      font-size: 14px;
      min-width: 170px;
      margin: 16px auto 0 0; }

    @include sm {
      flex: 1 1 auto;
      flex-direction: row;
      flex: 1 1 auto;
      margin: 0;
      gap: 10px; } } }
